//  Core list block styling

@use "../../utils" as *;

ul.wp-block-list {
	padding-inline-start: 1.7ch;

	li {
		&::marker {
			color: $color-secondary;
		}
	}
}

.wp-block-list.is-style-tags {
	display: flex;
	flex-wrap: wrap;
	gap: $spacing-small;
	padding-inline-start: unset;
	font-size: $font-size-small;

	li {
		background-color: $color-tertiary;
		border-radius: $radius-small;
		padding: calc($spacing-small * 0.75) $spacing-small;
		list-style: none;
	}
}

.wp-block-list.is-style-checks {
    padding-inline-start: unset;

	ul {
		padding-inline-start: unset;
	}

	li {
		position: relative;
		list-style: none;
		padding-inline-start: 1.3em;

		&::before {
			content: "";
			position: absolute;
			left: 0;
			top: 0.15em;
			width: 1em;
			height: 1.2em;
			background: url("data:image/svg+xml,%3Csvg%20width%3D%221em%22%20height%3D%221em%22%20viewBox%3D%220%200%2036%2036%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cg%20clip-path%3D%22url(%23clip0_4835_57038)%22%3E%3Cpath%20d%3D%22M0.947433%2020.5398C0.0840245%2016.0241%202.11175%2011.1018%205.59601%207.26171C9.08011%203.42175%2013.8788%200.82373%2018.3108%200.82373C22.8851%200.82373%2026.9625%202.08711%2029.8809%204.48143C32.7814%206.86103%2034.596%2010.4034%2034.596%2015.1116C34.596%2019.8519%2032.7542%2024.8428%2029.767%2028.6426C26.7779%2032.4449%2022.7164%2034.9685%2018.3108%2034.9685C13.749%2034.9685%209.86585%2033.6876%206.90538%2031.2733C3.9464%2028.8602%201.85137%2025.2678%200.947435%2020.5398L0.210955%2020.6806L0.947433%2020.5398Z%22%20stroke%3D%22%23A37FCC%22%20stroke-width%3D%221.5%22%2F%3E%3Cpath%20d%3D%22M10.5329%2020.7369C8.98499%2018.6789%2011.1778%2016.6209%2012.4677%2018.2673C14.0543%2020.2924%2014.9186%2023.0693%2016.4664%2021.1485C18.0143%2019.2277%2022.8876%2010.9956%2024.7218%2010.9956C26.3987%2010.9956%2026.2697%2012.0932%2025.2378%2013.6024C24.2059%2015.1116%2018.1433%2025.2645%2015.5635%2024.9901C12.9837%2024.7157%2010.5329%2020.7369%2010.5329%2020.7369Z%22%20fill%3D%22%23A37FCC%22%2F%3E%3C%2Fg%3E%3Cdefs%3E%3CclipPath%20id%3D%22clip0_4835_57038%22%3E%3Crect%20width%3D%2235.3459%22%20height%3D%2235.6448%22%20fill%3D%22white%22%20transform%3D%22translate(0%200.0732422)%22%2F%3E%3C%2FclipPath%3E%3C%2Fdefs%3E%3C%2Fsvg%3E")
